#collection-txt{
    border-right: solid 2px rgba(240, 255, 255, 0.342);
    border-bottom: none;
}

#triangle-code{
    z-index: -1;
    position: absolute;
    display : inline-block;
    height : 0;
    width : 0;
    margin-top: -50px;
    border-right : 200px solid transparent;
    border-bottom : 170px solid rgba(184, 184, 184, 0.6);
    border-left : 200px solid transparent;
    filter: blur(50px);
}

/* Télephone  */
@media (max-width: 575.98px) { 

    #collection-txt{
        border-right: none;
        border-bottom: solid 2px rgba(240, 255, 255, 0.342);
    }
  
}

/* Télephone Grand */
@media (max-width: 767.98px) { 

    #collection-txt{
        border-right: none;
        border-bottom: solid 2px rgba(240, 255, 255, 0.342);
    }

}

