body, html {
  box-sizing: border-box;  
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 15px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color:whitesmoke;
  scroll-behavior: smooth;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: var(--main-color-back);
}

li{
  list-style: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

a{
  text-decoration: none;
  color: azure;
}

a:hover{
  color: azure;
}

span{
  font-size: 15px;
}

p{
  font-size: 15px;
}

.btn-dark {
  --bs-btn-color: #fff;
  --bs-btn-bg: #22252B;
  --bs-btn-border-color: #22252B;
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: #222229;
  --bs-btn-hover-border-color: #222229;
  --bs-btn-focus-shadow-rgb: 66,70,73;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: #222229;
  --bs-btn-active-border-color: #222229;
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #fff;
  --bs-btn-disabled-bg: #22252B;
  --bs-btn-disabled-border-color: #22252B;
}

#root{
  min-height: 100%;
  min-width: 100%;
}

#introduction{
  padding: 7vw;
  -webkit-backdrop-filter: blur(30px);
  -ms-backdrop-filter: blur(30px);
  backdrop-filter: blur(30px);
  border-top-left-radius: 5px;
}

#reponse{
  height: 30vh;
  padding: 5vw;
  padding-top: 1vh;
  padding-bottom: 0;
}

#Introduction_Telephone{
  z-index: 99;
  display: block!important;
  position: relative!important;
  padding: 35px 1px 0px 0px;
  transition: 0.4s;
}

#text_intro{
  transition: 0.4s;
  font-size: 35px;
  overflow: hidden;
  color: azure;
  font-weight: 390;
}

#AR-button{
  z-index: 12;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

#answer{
  -webkit-user-select: text; /* Safari */
  -ms-user-select: text; /* IE 10 and IE 11 */
  user-select: text; /* Standard syntax */
}

.question{
  transition: 0.1s;
}

.question:hover{
  background-color: var(--main-color-accent);
}

.lien-web{
  transition: 0.1s;
}

.lien-web:hover{
  background-color: var(--main-color-accent);
}

.bas{
  float: inline-end;
}

.droite{
  float: inline-end;
}

.collection{
  background-color: var(--main-color-back);
}

.img-part{
  height: 80px;
  width: 80px;
  margin: 5px;
  border-radius: 5px;
}

/* Télephone  */
@media (max-width: 575.98px) { 

  #introduction{
    padding: 1vw;
    padding-bottom: 3vh;
    border-top-left-radius: 0px;
  }

  #reponse{
    height: 20vh;
  }

  #Introduction_Telephone{
    display: block!important;
    position: relative!important;
  }

  #NFT{
    height: 50vh;
  }

}

/* Télephone Grand */
@media (max-width: 767.98px) { 

  #introduction{
    padding: 1vw;
    padding-bottom: 3vh;
    border-top-left-radius: 0px;
  }

  #reponse{
    height: 20vh;
  }

  #Introduction_Telephone{
    display: block!important;
    position: relative!important;
  }

  #NFT{
    height: 50vh;
  }

}

